import { IonButton, IonCol, IonGrid, IonRow, IonText } from "@ionic/react";
import React from "react";
import { useHistory } from "react-router";

const Navbar: React.FC = () => {
  const history = useHistory();
  return (
    <>
      <IonRow>
        <IonCol>
          <IonButton onClick={() => history.push("/")} shape="round">
            <IonText className="content-text1-dark">Home</IonText>
          </IonButton>
        </IonCol>
        <IonCol>
          <IonButton onClick={() => history.push("aboutme#/")} shape="round">
            <IonText className="content-text1-dark">About Me</IonText>
          </IonButton>
        </IonCol>
        <IonCol>
          <IonButton onClick={() => history.push("projects#/")} shape="round">
            <IonText className="content-text1-dark">Projects</IonText>
          </IonButton>
        </IonCol>
        <IonCol>
          <IonButton onClick={() => history.push("coursework#/")} shape="round">
            <IonText className="content-text1-dark">Coursework</IonText>
          </IonButton>
        </IonCol>
        <IonCol>
          <IonButton onClick={() => history.push("contact#/")} shape="round">
            <IonText className="content-text1-dark">Contact Me</IonText>
          </IonButton>
        </IonCol>
      </IonRow>
    </>
  );
};

export default Navbar;
